@font-face {
  font-family: 'TTLakes';
  src: url(./fonts/TTLakes-Bold.woff2) format("woff2"), url(./fonts/TTLakes-Bold.woff) format("woff");
  font-weight: 700; }

@font-face {
  font-family: 'TTLakes';
  src: url(./fonts/TTLakes-Medium.woff2) format("woff2"), url(./fonts/TTLakes-Medium.woff) format("woff");
  font-weight: 500; }

@font-face {
  font-family: 'TTLakes';
  src: url(./fonts/TTLakes-Regular.woff2) format("woff2"), url(./fonts/TTLakes-Regular.woff) format("woff");
  font-weight: 400; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 16px;
  background-color: white; }

@media only screen and (min-width: 1120px) {
  .header {
    display: none; } }

.header__logo {
  margin-right: auto;
  position: relative; }

.header__burger-btn {
  background-color: white;
  border: white;
  padding: 0;
  position: relative;
  margin-right: 16px;
  cursor: pointer; }

@media only screen and (min-width: 768px) {
  .header__burger-btn {
    margin-right: 42px; } }

@media only screen and (min-width: 768px) {
  .header__burger-btn::after {
    content: '';
    display: block;
    position: absolute;
    top: 20px;
    right: -40px;
    width: 32px;
    height: 2px;
    background: #eaeaea;
    transform: rotate(90deg); } }

.header__icon-link:nth-child(5)::before {
  content: '';
  display: block;
  position: absolute;
  top: 20px;
  left: -40px;
  width: 32px;
  height: 2px;
  background: #eaeaea;
  transform: rotate(90deg); }

.header__nav-menu {
  display: flex; }

.header__icon-link {
  margin-left: 16px;
  position: relative; }

.header__icon-link[hidden] {
  display: none; }

@media only screen and (min-width: 768px) {
  .header__icon-link[hidden] {
    display: block; } }

.header__icon-link--margin {
  margin-right: 35px; }

.services-section {
  display: block;
  background: #f8f8f8; }

.services-section__warapper-header {
  display: flex;
  -moz-column-gap: 0px;
       column-gap: 0px;
  justify-content: space-between;
  margin-top: 42px; }

.services-section__header::before {
  content: '';
  display: block;
  position: absolute;
  top: 0px;
  left: -16px;
  border-radius: 5px;
  width: 4px;
  height: 40px;
  background: #41f6d7; }

.services-section__header {
  position: relative;
  margin-left: 16px;
  margin-bottom: 24px;
  font-size: 28px;
  line-height: 40px; }

.services-section__application-list {
  display: none; }

@media only screen and (min-width: 1120px) {
  .services-section__application-list {
    display: flex; } }

ul[class] {
  list-style: none; }

ul[class],
ol[class] {
  padding: 0; }

.services-section__application-list-item {
  display: flex;
  align-items: center;
  margin-right: 30px; }

.services-section__application-list-item span {
  margin-right: 10px; }

.services-section__list {
  display: flex;
  white-space: nowrap;
  overflow: hidden; }

@media only screen and (min-width: 320px) {
  .services-section__list {
    overflow: scroll; } }

@media only screen and (min-width: 768px) {
  .services-section__list {
    overflow: scroll; } }

@media only screen and (min-width: 1120px) {
  .services-section__list {
    overflow: inherit; } }

@media only screen and (min-width: 1120px) {
  .services-section__list {
    flex-wrap: wrap; } }

.services-section__nav-menu {
  margin-bottom: 40px; }

.services-section__link {
  width: 250px;
  margin-right: 30px;
  padding: 8px 16px; }

@media only screen and (min-width: 1120px) {
  .services-section__link {
    display: inline-block;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content; } }

.services-section__link a {
  color: #7e7e82; }

.services-section__link:nth-child(1) {
  background: #ffffff;
  border: 2px solid #b8ffec;
  border-radius: 6px;
  padding: 8px 16px;
  margin-left: 16px; }

.services-section__link--margin {
  margin-left: 16px; }

.services-section__text {
  padding: 0 16px;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 16px;
  letter-spacing: 0.2px; }

.services-section__text--small {
  min-width: 312px; }

@media only screen and (min-width: 768px) {
  .services-section__text--small {
    display: none; } }

.services-section__text--middle {
  /* max-width: 320px; */
  display: none; }

@media only screen and (min-width: 768px) {
  .services-section__text--middle {
    display: block; } }

@media only screen and (min-width: 1120px) {
  .services-section__text--middle {
    display: none; } }

.services-section__text--large {
  display: none; }

@media only screen and (min-width: 1120px) {
  .services-section__text--large {
    display: block; } }

.services-section__btn {
  display: flex;
  background-color: #f8f8f8;
  border: #f8f8f8;
  margin-left: 3px;
  margin-bottom: 16px; }

.services-section__wrapper-content {
  -moz-column-gap: 1em;
       column-gap: 1em;
  flex-basis: 0%;
  display: flex;
  position: relative;
  flex-direction: column;
  margin: 0;
  margin-bottom: 24px; }

@media only screen and (min-width: 768px) {
  .services-section__wrapper-content {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 32px; } }

@media only screen and (min-width: 768px) {
  .services-section__img {
    margin-right: 32px; } }

.services-section__img {
  box-sizing: border-box;
  border-radius: 10px; }

@media screen and (min-width: 768px) {
  .services-section__img {
    min-width: 50%;
    margin: 0 24px; } }

@media screen and (min-width: 1120px) {
  .services-section__img {
    min-width: 50%;
    margin: 0 32px 0 38px; } }

.wrapper-menu {
  position: absolute;
  height: 100%;
  z-index: 100;
  position: fixed; }

@media only screen and (min-width: 1120px) {
  .wrapper-menu {
    position: static; } }

html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  max-width: 1440px;
  min-height: 100%;
  padding: 0;
  margin: 0 auto;
  font-size: 14px;
  line-height: 18px;
  color: #1b1c21;
  font-family: 'TTLakes';
  background: #f8f8f8;
  position: relative; }

.no-scroll {
  position: fixed;
  width: 100%;
  height: 100%; }

.main {
  flex: 1 0 auto; }

img[hidden] {
  display: none; }

@media (min-width: 321px) {
  img[hidden] {
    display: block; } }

img {
  max-width: 100%;
  display: block; }

a {
  text-decoration: none; }

ul[class] {
  list-style: none; }

ul[class],
ol[class] {
  padding: 0; }

.wrapper-menu {
  position: absolute;
  height: 100%;
  z-index: 100;
  position: fixed; }

@media only screen and (min-width: 1120px) {
  .wrapper-menu {
    position: static; } }

.wrapper {
  width: 100vw;
  display: flex;
  flex-direction: column;
  min-height: 100%; }

@media only screen and (min-width: 1120px) {
  .wrapper {
    flex-direction: row; } }

.aside {
  z-index: 10;
  display: flex;
  box-shadow: rgba(14, 24, 80, 0.2) 16px 0px 52px;
  flex-direction: column;
  background-color: white;
  height: 2025px;
  width: 100vw; }

@media only screen and (min-width: 360px) {
  .aside {
    width: 320px; } }

@media only screen and (min-width: 1120px) {
  .aside {
    display: flex; } }

.aside__header {
  display: flex;
  justify-content: space-between; }

.aside__back-btn {
  margin-left: 16px;
  margin-top: 16px;
  cursor: pointer; }

.aside__logo {
  margin-right: auto;
  margin-left: 17px;
  margin-top: 16px; }

.aside__search-btn {
  background-color: transparent;
  border: transparent;
  padding: 0;
  margin-right: 16px;
  margin-top: 16px; }

.aside__nav-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

@media only screen and (min-width: 320px) {
  .aside__link:last-child {
    margin-bottom: 100px; } }

@media only screen and (min-width: 768px) {
  .aside__link:last-child {
    margin-bottom: 268px; } }

@media only screen and (min-width: 1120px) {
  .aside__link:last-child {
    margin-bottom: 250px; } }

.aside__link:first-child {
  margin-top: 32px; }

.aside__link {
  display: flex;
  margin-bottom: 24px;
  margin-left: 16px;
  position: relative;
  align-items: center;
  font-weight: 500; }

.aside__link:hover {
  color: #7e7e82; }

.aside__link:hover::before {
  content: '';
  display: block;
  position: absolute;
  left: -16px;
  border-radius: 5px;
  width: 4px;
  height: 35px;
  background: #41f6d7; }

.aside__contact {
  display: flex; }

.contact__icon:first-child {
  margin-left: 16px; }

.aside__select-language {
  display: flex;
  font-weight: 500; }

.select-language__item:hover {
  color: #7e7e82; }

.aside__contact-wrapper {
  display: flex;
  flex-direction: column; }

.aside__mail {
  margin-bottom: 14px;
  margin-top: 12px;
  color: black;
  font-size: 16px;
  font-weight: 500;
  margin-left: 16px; }

.aside__phone {
  color: black;
  font-family: 'TT Lakes';
  font-size: 24px;
  font-weight: 700;
  margin-left: 16px; }

.aside-header .contact__icon {
  margin-right: 16px; }

@media only screen and (min-width: 1120px) {
  .aside__back-btn {
    display: none; } }

.select-language__item:first-child {
  margin-left: 16px; }

.select-language__item {
  margin-top: 10px;
  margin-bottom: 30px;
  margin-right: 8px; }
